@import url("https://fonts.googleapis.com/css2?family=Merriweather:wght@400;700&display=swap");
body {
  margin: 0;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #eff0f1;
}

* {
  box-sizing: border-box;
}

.App {
  padding: 50px;
  width: 100%;
  max-width: 1200px;
}

h1 {
  font-family: "Merriweather", serif;
}

.tags {
  list-style: none;
  padding: 0;
}
.tags li {
  display: inline-block;
  padding: 12px 18px;
  background-color: #fff;
  border-radius: 10px;
  margin-right: 10px;
  cursor: pointer;
  font-weight: 500;
  font-size: 18px;
  border: 1px solid transparent;
  transition: all 0.15s ease-in-out;
}
.tags li:hover {
  border-color: #000;
}
.tags li.active {
  background-color: #000;
  color: #fff;
}

.content {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 30px;
  grid-row-gap: 30px;
  margin-top: 40px;
}
@media (max-width: 1000px) {
  .content {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 600px) {
  .content {
    grid-template-columns: repeat(1, 1fr);
  }
}

.collection {
  max-width: 470px;
  background-color: #fff;
  padding: 30px;
  border-radius: 30px;
  box-shadow: 0 3px 0 rgba(0, 0, 0, 0.05);
  cursor: pointer;
  transition: all 0.15s ease-in-out;
}
.collection h4 {
  margin: 0;
  margin-top: 15px;
  font-size: 20px;
  font-family: "Merriweather", serif;
}
.collection:hover {
  box-shadow: 0 20px 30px rgba(0, 0, 0, 0.1);
  transform: translateY(-5px);
}
@media (max-width: 1000px) {
  .collection {
    width: 100%;
  }
}
.collection img {
  -o-object-fit: cover;
     object-fit: cover;
}
.collection__big {
  height: 250px;
  width: 100%;
  border-radius: 20px;
  margin-bottom: 15px;
}
.collection__bottom {
  display: flex;
  justify-content: space-between;
}
.collection__mini {
  width: 31%;
  height: 80px;
  border-radius: 20px;
}

.top {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.search-input {
  font-size: 16px;
  width: 250px;
  height: 50px;
  padding: 0 15px;
  border-radius: 10px;
  border: 1px solid rgba(0, 0, 0, 0.2);
  outline: none;
  transition: all 0.15s ease-in-out;
}
.search-input:focus {
  border: 1px solid rgba(0, 0, 0, 0.4);
}

.pagination {
  list-style: none;
  padding: 0;
  margin: 0;
  margin-top: 40px;
}
.pagination li {
  display: inline-block;
  padding: 10px 17px;
  background-color: white;
  font-size: 18px;
  border-radius: 10px;
  margin-right: 15px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  cursor: pointer;
}
.pagination li.active {
  background-color: black;
  color: #fff;
}